<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="newdetail">
          <div class="title">
            <h1>{{newdata.title}}</h1>
            <p>{{$t('data.Releasetime')}} {{newdata.createTime}}</p>
            <p>{{$t('data.author')}} {{newdata.author}}</p>
          </div>
          <img :src="newdata.banner" style="width: 100%;margin:0 0 .2rem 0">
          <div class="content" v-html="newdata.content"></div>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:this.$t('mycenter.news'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      newdata:''
    }
  },
  methods:{
    newdetail(){
      var that = this
      this.axios({
        url:this.Url + 'News.ashx',
        method:'post',
        params: {req:5,id:this.$route.query.id}
      }).then(res=>{
        if (res.data.data) {
          res.data.data.banner = that.imgUrl + res.data.data.banner
          that.newdata = res.data.data
        }
      }).catch(res=>{
        console.log(res)
      })
    }
  },
  created(){
    this.newdetail()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
  -webkit-touch-callout:none;
    -webkit-user-select:none;
    -khtml-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    user-select:none;
}
.newdetail{
  padding: .3rem
}
.title{
  margin-bottom: .3rem;
}
.title h1{
  font-size: .32rem;
  color: #222;
  border-bottom: 1px solid #eee;
  padding: 0 0 .2rem 0;
  font-weight: normal;
  margin-bottom: .2rem
}
.title p{
  font-size: .24rem;
  color: #888;
}
.content p{
  font-size: .28rem!important;
  text-align: justify;
}
.newbot{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: .3rem;
  border-top: 1px solid #f1f1f1
}
.logo img{
  width: .7rem;
  height: .7rem;
  border-radius: 50%;
}
.dwon img{
  width: 1.2rem;
  height: 1.2rem;
}
.newdw{
  padding-left: .2rem;
  font-size: .24rem;
}
.newdw p:nth-child(1){
  color:#278eed;
}
.newdw p:nth-child(2){
  color:#888;
}
</style>



